import React from "react";
import styled from "styled-components";

interface BoxLabellProps {
  children?: React.ReactNode;
  background?: string;
  triangle?: boolean;
  width?: number; 
  marginLeft?: number;
  onClick?: ()=> void;
}



const BoxLabel: React.FC<BoxLabellProps> = (props) => {
  const { width, children, marginLeft, background,triangle, onClick } = props; 

  const Left = styled.div`
    width: 0;
    height: 0;
    border-top: 4px solid transparent;
    border-bottom: 4px solid transparent;
    border-right: 7px solid ${background};
  `;

  const Styled = styled.div`
    display: inline-block;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
    border-radius: 4px;
    height: 22px;
    font-family: "Elice Digital Baeum",sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 10px;
    line-height: 22px;
    letter-spacing: 0.03em;
    color: #FFFFFF;
    padding-left: 10px;
    padding-right: 10px;
  `;

  return (
      <>
      {
      triangle === true ?
            <>
            <Left style={{   
                marginLeft: `${marginLeft}px`, 
            }}></Left>
            <Styled
                style={{
                    background: `${background}`, 
                    width: `${width}px`,  
                }}
                        onClick = {onClick}
                >
                {children}
            </Styled>   
            </>
      :
            <>
            <Styled
                style={{
                    background: `${background}`, 
                    width: `${width}px`,    
                    marginLeft: `${marginLeft}px`,  
                }}
                        onClick = {onClick}
                >
                {children}
            </Styled>   
            </>        
     }
             
    </>
    
  );
};


BoxLabel.defaultProps = {
    triangle: false,
    background: "#ffffff"
}

export default BoxLabel

