import { Box, Container, TextField } from "@mui/material"
import { useState } from "react"
import { BoxLabel, CardViewImage, ChipLarge, Footer, Header } from "../components"
import ButtonSquareSmall from "../components/ButtonSquareSmall"
import ChipLargeY from "../components/ChipLargeY"
import topImg from "../img/member_detail_teacher_group_image1.svg"
import { styled } from '@mui/material/styles';
import LinearProgress, { linearProgressClasses } from '@mui/material/LinearProgress';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select'
import ButtonSquare from "../components/ButtonSquare"
import ButtonSquareLine from "../components/ButtonSquareLine"
import ButtonSquareLarge from "../components/ButtonSquareLarge"
import DiaryThreapist from "./Diary/DiaryThreapist"
import ParentsReview from "./Diary/ParentsReview"
import ThreapistReview from "./Diary/ThreapistReview"

interface DiaryProps {

}


const DiaryChild: React.FC<DiaryProps> = (props) => {
    const [menu, setMenu] = useState(1);
    const [menuArray, setMenuArray] = useState([0, 1, 0, 0, 0]);

    const changeMenu = (num: number) => {
        setMenu(() => num);
        console.log("menu", menu);
        const newArr = [...menuArray];
        menuArray.map((item: number, index: number) => {
            if (num == index) {
                newArr[index] = 1;
            } else {
                newArr[index] = 0;
            }
            setMenuArray(() => newArr);
        }
        );
    }
    const clickMenu = (num: number) => {
        changeMenu(num);
    };

    let component = <DiaryThreapist></DiaryThreapist>;
    switch (menu) {
        case 1: component = (<DiaryThreapist></DiaryThreapist>); break;
        case 2: component = (<ThreapistReview></ThreapistReview>); break;
        case 3: component = (<ParentsReview></ParentsReview>); break;
        default: component = (<DiaryThreapist></DiaryThreapist>); break;
    }



    return (
        <div className="common-bg">
            <Header
                className=""
                goodroad="/asset/main/icon-goodroad.png"
                bell="/asset/main/icon-bell.png"
                kakao="/asset/main/icon-kakao.png"
                color="font-bold-s12-c573300"
            />
            <div className="media-wrapper">
                <Container></Container>
                <Container>
                    {/* ######################### Content Area Begin ######################### */}


                    <img src="/asset/img/treatment_main.svg" style={{ marginTop: 20 }} />

                    <div className="common-flex-row-wrap-vcenter" style={{ marginTop: 40, marginLeft: 10 }}>
                        <ChipLarge>임시메뉴</ChipLarge>
                        <ChipLarge onClick={() => clickMenu(1)} type={menuArray[1]}>치료일지:치료사</ChipLarge>
                        <ChipLarge onClick={() => clickMenu(2)} type={menuArray[2]}>치료사후기:보호자</ChipLarge>
                        <ChipLarge onClick={() => clickMenu(3)} type={menuArray[3]}>보호자후기:치료사</ChipLarge>
                    </div>





                    {component}

                    <div style={{ marginBottom: 20 }}></div>











                    {/* ######################### Content Area End   ######################### */}
                </Container>
                <Container></Container>
                <Footer></Footer>
            </div>
        </div>
    )

}

export default DiaryChild