import * as React from "react";
import PropTypes from "prop-types";
import Slider, { SliderThumb } from "@mui/material/Slider";
import { styled } from "@mui/material/styles";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import clsx from "clsx";
import { useState } from "react";



const AirbnbSlider = styled(Slider)(({ theme }) => ({
  color: "#3a8589",
  height: 3,
  padding: "13px 0",
  "& .MuiSlider-thumb": {
    height: 30,
    width: 34,
    backgroundColor: "#ECECEC",
    border: "0px solid currentColor",
    "&.second-thumb": {
      border: "0px dashed purple"
    },
    "&:hover": {
      boxShadow: "0 0 0 0px rgba(58, 133, 137, 0.16)"
    },
    "& .airbnb-bar": {
      height: 9,
      width: 1,
      marginLeft: 1,
      marginRight: 1
    },
    "&.first-thumb .airbnb-bar": {
      backgroundColor: "red"
    },
    "&.second-thumb .airbnb-bar": {
      backgroundColor: "currentColor"
    },
    '&:before': {
      display: 'none',
    }
  },
  "& .MuiSlider-track": {
    border: 'none',
    height: 3
  },
  "& .MuiSlider-rail": {
    color: theme.palette.mode === "dark" ? "#bfbfbf" : "#d8d8d8",
    opacity: theme.palette.mode === "dark" ? undefined : 1,
    height: 3
  }
}));
const marks = [
  {
    value: 0,
    label: '0세',
  },
  {
    value: 4,
    label: '4세',
  },
  {
    value: 8,
    label: '8세',
  },
  {
    value: 12,
    label: '12세',
  },
  {
    value: 16,
    label: '16세',
  },
];
function valuetext(value: number) {
  return `${value}세`;
}
interface AirbnbThumbComponentProps extends React.HTMLAttributes<unknown> {}
function AirbnbThumbComponent(props: AirbnbThumbComponentProps) {
  const { children, className, ...other } = props;
  //console.log("---" , other);
  const extraClassName =  other["aria-level"] === 0 ? "first-thumb" : "second-thumb";  
  return (
    <SliderThumb {...other} className={clsx(className, extraClassName)}>
      {children}
      <span className="rc-slider-handle-l" />
      {/* <span className="airbnb-bar" />
      <span className="airbnb-bar" />
      <span className="airbnb-bar" /> */}
      </SliderThumb>
  );
}

AirbnbThumbComponent.propTypes = {
  children: PropTypes.node
};



export default function SliderTest() {
  const [value, setValue] = React.useState<number[]>([2, 6]);
  const handleChange = (event: Event, newValue: number | number[]) => {
    //console.log("handleChange" , value[0]);
    setValue(newValue as number[]);
  };

  return (
      <>
      
        
        <AirbnbSlider
          components={{ Thumb: AirbnbThumbComponent }}
          getAriaLabel={(index) =>
            index === 0 ? "Minimum price" : "Maximum price"
          }
          marks={marks}
          value={value}
          onChange={handleChange}
          valueLabelDisplay="auto"
          getAriaValueText={valuetext}
          min={0} max={16}
        />
     
      </>
      
  );
}